.wcf__priceTable .title {
  display: inline-block;
}
.wcf__priceTable li {
  list-style: none;
}
.wcf__priceTable .pt-sale-price {
  font-size: 30px;
  line-height: 1;
}
.wcf__priceTable .pt-org-price {
  line-height: 1;
  text-decoration: line-through;
}
.wcf__priceTable .pt-currency {
  line-height: 1;
}
.wcf__priceTable .pt-feature li {
  position: relative;
  padding-bottom: 5px;
}
.wcf__priceTable svg {
  width: 1em;
  height: 1em;
}
.wcf__priceTable .ribbon {
  background-color: #0a0a0a;
  position: absolute;
  left: 250px;
  top: 20px;
  padding: 5px 7px 7px 7px;
  color: #fff;
  line-height: 1;
  text-align: center;
}
.wcf__priceTable.skin-pricing-table-1 {
  display: grid;
  align-items: center;
  grid-template-columns: 0.7fr 1fr 0.5fr;
  background-color: #233259;
  position: relative;
  overflow: hidden;
}
@media (max-width: 767px) {
  .wcf__priceTable.skin-pricing-table-1 {
    grid-template-columns: 1fr;
    padding-inline-end: 0;
    padding-bottom: 30px;
  }
}
.wcf__priceTable.skin-pricing-table-1 .pt-header {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #FCFAF5;
}
.wcf__priceTable.skin-pricing-table-1 .header-top {
  padding: 15px;
  background: #7DDED8;
}
@media (max-width: 767px) {
  .wcf__priceTable.skin-pricing-table-1 .header-top {
    height: auto;
  }
}
.wcf__priceTable.skin-pricing-table-1 .header-top .icon {
  font-size: 30px;
  color: #16254C;
  text-align: center;
}
.wcf__priceTable.skin-pricing-table-1 .title-wrap {
  text-align: center;
}
.wcf__priceTable.skin-pricing-table-1 .sub-title {
  text-align: center;
}
.wcf__priceTable.skin-pricing-table-1 .title {
  color: #16254C;
  font-size: 18px;
}
.wcf__priceTable.skin-pricing-table-1 .price-wrap {
  padding: 15px;
  width: 100%;
  display: inline-flex;
  background-color: #FCFAF5;
  text-align: center;
}
.wcf__priceTable.skin-pricing-table-1 .pt-sale-price {
  font-size: 50px;
  line-height: 1;
  align-self: flex-end;
}
.wcf__priceTable.skin-pricing-table-1 .pt-currency {
  font-size: 16px;
  line-height: 1.5;
  align-self: flex-start;
}
.wcf__priceTable.skin-pricing-table-1 .pt-period {
  position: relative;
  padding-left: 7px;
  align-self: flex-end;
}
.wcf__priceTable.skin-pricing-table-1 .pt-org-price {
  color: #16254C;
  margin-inline-end: 10px;
  align-self: flex-end;
}
.wcf__priceTable.skin-pricing-table-1 .pt-feature {
  padding: 25px;
  position: relative;
  border-right: 1px solid #30406A;
}
@media (max-width: 767px) {
  .wcf__priceTable.skin-pricing-table-1 .pt-feature {
    padding: 0 15px;
  }
}
.wcf__priceTable.skin-pricing-table-1 .pt-feature li {
  color: #A4ABBB;
}
.wcf__priceTable.skin-pricing-table-1 .pt-feature i,
.wcf__priceTable.skin-pricing-table-1 .pt-feature svg {
  font-size: 14px;
  margin-inline-end: 5px;
}
.wcf__priceTable.skin-pricing-table-1 .ribbon {
  left: -35px;
  top: 25px;
  padding: 7px 50px 7px 50px;
  transform: rotate(-45deg);
}
.wcf__priceTable.skin-pricing-table-1 .wcf__btn {
  margin-top: 30px;
  text-align: center;
}
.wcf__priceTable.skin-pricing-table-2 {
  display: grid;
  position: relative;
  overflow: hidden;
}
.wcf__priceTable.skin-pricing-table-2 .price-wrap {
  display: inline-flex;
  padding: 15px 0;
  width: 100%;
}
.wcf__priceTable.skin-pricing-table-2 .pt-feature {
  border-top: 1px solid #ddd;
  padding: 20px 0;
}
.wcf__priceTable.skin-pricing-table-2 .pt-period {
  padding-left: 5px;
  align-self: flex-end;
}
.wcf__priceTable.skin-pricing-table-2 .pt-org-price {
  margin-right: 10px;
  align-self: flex-end;
}

.elementor-widget-wcf--a-pricing-table.btn-align-stretch .wcf__btn a {
  width: 100%;
}